import { createApp } from "vue"

import Cookies from "js-cookie"

import "normalize.css/normalize.css" // a modern alternative to CSS resets

import ElementPlus from "element-plus"
import * as ElementPlusIconsVue from "@element-plus/icons-vue" // 导入ElementPlus图标
import "element-plus/dist/index.css" // 导入ElementPlus默认样式
import zhLocale from "element-plus/lib/locale/lang/zh-cn" // 导入ElementPlus中文语言包
// import "element-plus/theme-chalk/dark/css-vars.css" // 暗黑模式导入

import "./styles/element-variables.scss" // 导入自定义颜色风格
import "@/styles/index.scss" // 导入自定义全局样式

import App from "./App.vue"
import store from "./store"
import router from "./router"
import "./router/permission" // 路由守卫、权限控制（注意：要在store和router之后引入）

import useSvgIcon from "./icons"
import useErrorLog from "./utils/error-log"

import "./fonts/font.css" // 引入字体

const app = createApp(App) // 创建实例

useSvgIcon(app) // 注册SVG图标

useErrorLog(app) // 使用ErrorLog

//使用ElementPlus框架
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component) //使用ElementPlus图标
}

app.use(ElementPlus, {
  size: Cookies.get("size") || "default", // set default size
  locale: zhLocale,
})
// zhLocale.el.pagination = {
//   goto: "跳至",
//   pagesize: "条/页",
//   total: `共计 {total} 条`,
//   pageClassifier: "页",
// }

app.use(router)
app.use(store)
app.mount("#app")
