import axios from "axios"
import { MessageBox, ElMessage } from "element-plus"
import store from "@/store"
import { getToken } from "@/utils/auth"
// import { errorLog } from "@/settings"

// 创建axios实例: ajax
const ajax = axios.create({
  baseURL: process.env.VUE_APP_BASE_API, // url = base url + request url
  withCredentials: true, // 跨域请求时是否发送cookie（后端使用Session时需要开启）
  timeout: 30000, // request timeout 请求超时时间
})

// request interceptor 请求拦截器
ajax.interceptors.request.use(
  (config) => {
    // do something before request is sent
    console.log("请求 request:", config) //调试
    if (store.getters.token) {
      // 让每个请求携带自定义token
      config.headers["Authorization"] = `Bearer ${getToken()}` //加上'Bearer '，否则后端认证失败
    }
    return config
  },
  (error) => {
    // do something with request error
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor 响应拦截器
ajax.interceptors.response.use(
  /**
   * If you want to get http information such as headers or status
   * Please return  response => response
   */

  /**
   * 通过response自定义code（这里仅为示例）来表示请求状态，当code返回如下情况为权限有问题，登出并返回到登录页
   * 也可以通过HTTP状态码来判断，逻辑可写在下面error中
   */
  (response) => {
    console.log("响应 response:", response) //调试
    return response
    // const res = response.data
    // //如果自定义的code不等于20000，视为错误
    // if (res.code !== 20000) {
    //   ElMessage({
    //     message: res.message || "Error",
    //     type: "error",
    //     duration: 5 * 1000,
    //   })
    //   // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
    //   if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
    //     MessageBox.confirm(
    //       "您已退出登录。可以重新登录，或者点击取消以继续留在该页面。",
    //       "确定退出登录",
    //       {
    //         confirmButtonText: "重新登录",
    //         cancelButtonText: "取消",
    //         type: "warning",
    //       }
    //     ).then(() => {
    //       store.dispatch("user/resetToken").then(() => {
    //         location.reload() //为了重新实例化vue-router对象，避免bug
    //       })
    //     })
    //   }
    //   return Promise.reject(new Error(res.message || "Error"))
    // } else {
    //   return response //返回data还是response？
    // }
  },
  (error) => {
    console.log("err" + error) // for debug
    const statusText = {
      400: "(400)错误请求",
      401: "(401)未授权！请重新登录",
      403: "(403)拒绝访问",
      404: "(404)服务器找不到请求的网页",
      405: "(405)禁用请求中指定的方法",
      408: "(408)请求超时",
      415: "(415)不支持的媒体格式",
    }
    ElMessage({
      //消息优先级：后端定义的错误消息 || 前端翻译的错误消息 || 默认错误消息
      message: error.response.data || statusText[error.response.status] || error.message,
      type: "error",
      duration: 3 * 1000,
    })
    return Promise.reject(error)
  }
)

export default ajax
