import router from "/"
import store from "../store"
// import { ElMessage } from "element-plus"
import NProgress from "nprogress" // progress bar
import "nprogress/nprogress.css" // progress bar style
import { getToken, getExpires } from "@/utils/auth" // get token from cookie
import getPageTitle from "@/utils/get-page-title"

NProgress.configure({ showSpinner: false }) // showSpinner: 是否显示旋转控件

const whiteList = ["/reader", "/login", "/auth-redirect"] // no redirect whitelist 免登录白名单

router.beforeEach(async (to, from, next) => {
  NProgress.start() // 进度条开始
  document.title = getPageTitle(to.meta.title) // 设置页面标题
  // 判断是否已登录（有token）
  if (getToken()) {
    // 判断访问的是不是登录页
    if (to.path === "/login") {
      next({ path: "/" }) // 已登录的情况下访问登录页，直接重定向到主页
      NProgress.done() // 手动结束进度条，因为router.afterEach有时不会被触发
    } else {
      // 判断当前用户是否已取得角色信息
      if (store.getters.roles && store.getters.roles.length > 0) {
        const expires_in = getExpires() - Math.round(new Date().getTime() / 1000) //token剩余有效期
        console.log(`------------${to.fullPath}-------------`) //调试
        console.log("Expires_In: ", expires_in) //调试
        // 判断token剩余有效期是否小于设定值（单位：秒）
        if (expires_in <= 2400) {
          store
            .dispatch("user/refreshToken") // 请求刷新token
            .catch((error) => {
              if (error.response.status === 401) {
                if (whiteList.indexOf(to.path) !== -1) {
                  next() // 在免登录白名单，直接进入
                } else {
                  // next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
                  // NProgress.done() // 手动结束进度条
                  location.reload() //否则刷新整个页面
                }
              }
            })
        }
        next() //当有用户角色信息的时候，说明所有可访问路由已生成，如访问没权限会自动进入404页面
      } else {
        try {
          // 获取用户信息，然后此处解构出roles使用
          const { roles } = await store.dispatch("user/getInfo")
          // 根据角色生成可访问的路由表。注意:把 roles 转换为数组!
          const accessRoutes = await store.dispatch("permission/generateRoutes", roles.split(","))
          // 动态添加可访问路由表
          // router.addRoutes(accessRoutes) // addRoutes方法弃用了，只能用addRoute方法逐个添加
          accessRoutes.forEach((route) => router.addRoute(route))
          // 确保addRoutes已完成 hack方法
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
        } catch (error) {
          // 删除 token 并回到登录页面重新登录
          await store.dispatch("user/resetToken")
          // ElMessage.error(error || "Has Error") // 已在response拦截中处理错误信息，此处不用再显示
          next(`/login?redirect=${to.path}`)
          NProgress.done() // 手动结束进度条
        }
      }
    }
    // 如果没有token
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next() // 在免登录白名单，直接进入
    } else {
      next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
      NProgress.done() // 手动结束进度条
    }
  }
})

router.afterEach(() => {
  NProgress.done() // 结束进度条
})
