//实例化Vue的时候只挂载constantRoutes
const router = createRouter({
  history: createWebHashHistory(), // hash模式：createWebHashHistory，history模式：createWebHistory
  scrollBehavior: () => ({ y: 0 }), // 滚动条滚到顶部
  routes: constantRoutes.concat(),
})

