import { login, register, logout, getInfo, refreshToken } from "@/api/user"
import {
  getToken,
  setToken,
  removeToken,
  getExpires,
  setExpires,
  removeExpires,
} from "@/utils/auth"
import router, { resetRouter } from "@/router"

const state = {
  token: getToken(),
  expires: getExpires(),
  name: "",
  avatar: "",
  introduction: "",
  roles: [],
}

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_EXPIRES: (state, expires) => {
    state.expires = expires
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password, captcha } = userInfo
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password, captcha: captcha }) //发送Ajax请求
        .then((response) => {
          const { data } = response
          console.log("登录成功:", data) //调试
          // token的属性名要与后端对应
          commit("SET_TOKEN", data.access_token) //token保存到state
          commit("SET_EXPIRES", data.expires_in) //expires保存到state
          setToken(data.access_token) //token保存到cookie
          setExpires(data.expires_in) //expires保存到cookie
          resolve()
        })
        .catch((error) => {
          console.log("登录失败:", error) //调试
          reject(error)
        })
    })
  },

  // user register
  register({ commit }, userInfo) {
    const { username, password, captcha } = userInfo
    return new Promise((resolve, reject) => {
      register({ username: username.trim(), password: password, captcha: captcha }) //发送Ajax请求
        .then((response) => {
          const { data } = response
          console.log("注册成功:", data) //调试
          resolve()
        })
        .catch((error) => {
          console.log("注册失败:", error) //调试
          reject(error)
        })
    })
  },

  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo() //发送Ajax请求
        .then((response) => {
          const { data } = response
          if (!data) {
            reject("Verification failed, please Login again.")
          }
          // 解构赋值，属性名要与后端对应
          const { roles, userName, portrait, introduction } = data
          const rolesArr = roles.split(",") // 注意:把 roles 转换为数组!
          // 角色信息必须是非空数组
          if (!rolesArr || rolesArr.length <= 0) {
            reject("getInfo: roles must be a non-null array!")
          }
          commit("SET_ROLES", rolesArr)
          commit("SET_NAME", userName)
          commit("SET_AVATAR", portrait)
          commit("SET_INTRODUCTION", introduction)
          resolve(data)
        })
        .catch((error) => {
          reject(error)
        })
    })
  },

  refreshToken({ commit, state }) {
    return new Promise((resolve, reject) => {
      refreshToken() //发送Ajax请求
        .then((response) => {
          const { data } = response
          console.log("刷新Token成功:", data) //调试
          // token的属性名要与后端对应
          commit("SET_TOKEN", data.access_token) //token保存到state
          commit("SET_EXPIRES", data.expires_in) //expires保存到state
          setToken(data.access_token) //token保存到cookie
          setExpires(data.expires_in) //expires保存到cookie
          resolve()
        })
        .catch((error) => {
          console.log("刷新Token失败:", error) //调试
          reject(error)
        })
    })
  },

  // user logout
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      logout() //发送Ajax请求
        .then(() => {
          dispatch("resetToken")
          resetRouter() //重置路由
          dispatch("tagsView/delAllViews", null, { root: true }) // 重置visitedViews和cachedViews
          resolve()
        })
        .catch((error) => {
          dispatch("resetToken")
          resetRouter() //重置路由
          dispatch("tagsView/delAllViews", null, { root: true }) // 重置visitedViews和cachedViews
          reject(error)
        })
    })
  },

  // remove token
  resetToken({ commit }) {
    return new Promise((resolve) => {
      commit("SET_TOKEN", "")
      commit("SET_EXPIRES", "")
      commit("SET_ROLES", [])
      removeToken()
      removeExpires()
      resolve()
    })
  },

  // dynamically modify permissions
  async changeRoles({ commit, dispatch }, role) {
    const token = role + "-token"
    commit("SET_TOKEN", token)
    setToken(token)
    const { roles } = await dispatch("getInfo")
    resetRouter()
    // generate accessible routes map based on roles
    const accessRoutes = await dispatch("permission/generateRoutes", roles, { root: true })
    // dynamically add accessible routes
    // router.addRoutes(accessRoutes) // addRoutes方法弃用了，只能用addRoute方法逐个添加
    accessRoutes.forEach((route) => router.addRoute(route))
    // reset visited views and cached views
    dispatch("tagsView/delAllViews", null, { root: true })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
}
