<template>
  <component :is="type" v-bind="linkProps(to)">
    <slot />
  </component>
</template>

<script>
import { isExternal } from "@/utils/validate"

export default {
  props: {
    to: {
      type: String,
      required: true,
    },
  },
  computed: {
    isExternal() {
      return isExternal(this.to)
    },
    type() {
      //判断链接类型，如果是外链则返回a标签，否则返回router-link标签
      if (this.isExternal) {
        return "a"
      }
      return "router-link"
    },
  },
  methods: {
    linkProps(to) {
      if (this.isExternal) {
        return {
          href: to,
          target: "_blank",
          rel: "noopener",
        }
      }
      return {
        to: to,
      }
    },
  },
}
</script>
