<template>
  <div class="navbar">
    <!-- 侧边收展按钮 -->
    <hamburger
      class="hamburger-container"
      :toggleClick="toggleSideBar"
      :isActive="sidebar.opened"
    ></hamburger>
    <!-- 面包屑导航 -->
    <breadcrumb id="breadcrumb-container" class="breadcrumb-container" />
    <!-- 右上角菜单栏 -->
    <div class="right-menu">
      <!-- 菜单按钮 -->
      <template v-if="device !== 'mobile'">
        <search id="header-search" class="right-menu-item" />
        <error-log class="errLog-container right-menu-item hover-effect" />
        <screenfull id="screenfull" class="right-menu-item hover-effect" />
        <el-tooltip content="Global Size" effect="dark" placement="bottom">
          <size-select id="size-select" class="right-menu-item hover-effect" />
        </el-tooltip>
      </template>
      <!-- 用户信息与下拉菜单 -->
      <el-dropdown
        class="avatar-container right-menu-item hover-effect"
        trigger="click"
        size="default"
      >
        <div class="avatar-wrapper el-dropdown-link">
          <el-image :src="avatar" class="user-avatar">
            <!-- 如果avatar指定的图片未找到，则用下面的图片代替 -->
            <template v-slot:error>
              <img
                src="https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif"
                class="user-avatar"
              />
            </template>
          </el-image>
          <div style="display: inline-block; width: 40px; margin-left: 5px">
            <div
              style="
                width: 100px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 14px;
                font-weight: bold;
              "
            >
              {{ name }}
            </div>
            <el-icon><caret-bottom /></el-icon>
          </div>
        </div>
        <template v-slot:dropdown>
          <el-dropdown-menu>
            <router-link to="/profile/index">
              <el-dropdown-item>Profile</el-dropdown-item>
            </router-link>
            <router-link to="/">
              <el-dropdown-item>Dashboard</el-dropdown-item>
            </router-link>
            <a target="_blank" href="https://https://github.com/Folo92/YunAdmin.Web/">
              <el-dropdown-item>Github</el-dropdown-item>
            </a>
            <el-dropdown-item divided @click="logout">
              <span style="display: block">Log Out</span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </template>
      </el-dropdown>
      <!-- <div style="display: inline-block; width: 80px">使用帮助</div> -->
    </div>
  </div>
</template>

<script>
// import { ElMessage } from "element-plus"
import { mapGetters } from "vuex"
import Hamburger from "@/components/Hamburger"
import Breadcrumb from "@/components/Breadcrumb"
import Search from "@/components/HeaderSearch"
import ErrorLog from "@/components/ErrorLog"
import Screenfull from "@/components/Screenfull"
import SizeSelect from "@/components/SizeSelect"

export default {
  name: "NavBar",
  components: {
    Hamburger,
    Breadcrumb,
    Search,
    ErrorLog,
    Screenfull,
    SizeSelect,
  },
  computed: {
    ...mapGetters(["sidebar", "avatar", "name", "device"]),
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch("app/toggleSideBar")
    },
    async logout() {
      await this.$store
        .dispatch("user/logout")
        // this.$router.push(`/login?redirect=${this.$route.fullPath}`)
        .then(() => {
          this.$router.push(`/login`)
        })
        // this.$router.push(`/login`)
        .catch(() => {
          this.$router.push(`/login`)
        })
    },
  },
}
</script>

<style lang="scss" scoped>
.navbar {
  height: 50px;
  overflow: hidden;
  position: relative;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);

  .hamburger-container {
    line-height: 58px;
    height: 100%;
    float: left;
    cursor: pointer;
    transition: background 0.3s;
    -webkit-tap-highlight-color: transparent;

    &:hover {
      background: rgba(0, 0, 0, 0.025);
    }
  }

  .breadcrumb-container {
    float: left;
  }

  .errLog-container {
    display: inline-block;
    vertical-align: top;
  }

  .right-menu {
    float: right;
    height: 100%;
    line-height: 50px;

    &:focus {
      outline: none;
    }

    .right-menu-item {
      display: inline-block;
      padding: 0 8px;
      height: 100%;
      font-size: 18px;
      color: #5a5e66;
      vertical-align: text-bottom;

      &.hover-effect {
        top: 16px; //add
        cursor: pointer;
        transition: background 0.3s;

        &:hover {
          background: rgba(0, 0, 0, 0.025);
        }
      }
    }

    .avatar-container {
      margin-right: 50px;

      .avatar-wrapper {
        margin-top: -10px;
        position: relative;

        .user-avatar {
          cursor: pointer;
          width: 36px;
          height: 36px;
          border-radius: 10px;
        }

        .el-icon-caret-bottom {
          cursor: pointer;
          position: absolute;
          right: -20px;
          top: 25px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
