<template>
  <!-- 菜单图标 -->
  <el-icon v-if="isElIcon()" class="sub-el-icon">
    <component :is="icon.slice(8)"></component>
  </el-icon>
  <svg-icon v-else :icon-class="icon" />
  <!-- 菜单文字 -->
  <span class="menu-title">
    <slot name="title">{{ title }}</slot>
  </span>
</template>
<script>
export default {
  name: "ItemInner",
  props: {
    icon: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
  },
  methods: {
    isElIcon() {
      return this.icon.includes("el-icon-")
    },
  },
}
</script>

<style scoped>
.sub-el-icon {
  color: currentColor;
  width: 1em;
  height: 1em;
}
</style>
