<template>
  <section class="app-main">
    <router-view v-slot="{ Component, route }">
      <transition :name="route.meta.transition || 'fade-transform'" mode="out-in">
        <keep-alive :include="getCachedViews()" max="10">
          <component :is="Component" :key="routePath" v-if="isViewAlive" />
        </keep-alive>
      </transition>
    </router-view>
    <!-- <transition name="fade-transform" mode="out-in">
      <keep-alive :include="cachedViews">
        <router-view :key="routePath" />
      </keep-alive>
    </transition> -->
  </section>
</template>

<script>
export default {
  name: "AppMain",
  data() {
    return {
      isViewAlive: true, //用于刷新页面
      cachedViews: "",
    }
  },
  props: ["reloadPath"], //用于刷新页面
  watch: {
    //用于刷新页面
    reloadPath() {
      if (this.reloadPath === this.routePath) {
        this.isViewAlive = false
        this.$nextTick(() => {
          this.isViewAlive = true
        })
      }
    },
  },
  methods: {
    getCachedViews() {
      // console.log("cachedViews:", this.$store.state.tagsView.cachedViews) //调试
      // console.log("visitedViews:", this.$store.getters.visitedViews)//调试
      return this.$store.state.tagsView.cachedViews //cachedViews用的是route的name属性
    },
  },
  computed: {
    // cachedViews() {
    //   return this.$store.state.tagsView.cachedViews//有问题，不能动态监听
    // },
    routePath() {
      return this.$route.path
    },
  },
}
</script>

<style lang="scss" scoped>
.app-main {
  /* 50= navbar  50  */
  min-height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  overflow: hidden;
}

.fixed-header + .app-main {
  padding-top: 50px;
}

.hasTagsView {
  .app-main {
    /* 84 = navbar + tags-view = 50 + 34 */
    min-height: calc(100vh - 84px);
  }

  .fixed-header + .app-main {
    padding-top: 84px;
  }
}
</style>

<style lang="scss">
// fix css style bug in open el-dialog
.el-popup-parent--hidden {
  .fixed-header {
    padding-right: 15px;
  }
}
</style>
