import SvgIcon from "@/components/SvgIcon" // 导入自定义svg-icon组件
export default function useSvgIcon(app) {
  app.component("svg-icon", SvgIcon) // 注册SVG图标
}

//导入所有svg图标
const req = require.context("./svg", false, /\.svg$/)
const requireAll = (requireContext) => requireContext.keys().map(requireContext)
requireAll(req)
